import { Component, OnInit } from '@angular/core';
import { CusViewHttpService } from '../../http/custom-view/custom-view.http.service';
import { ScreenageBean } from './bean/screenage.bean';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { TZB_HTTP_YX, TZB_HTTP_XIN, TZB_HTTP_XIN_C } from "app/@uisftech/common/service/http.constant";
import { Message } from 'primeng/primeng';
@Component({
  selector: 'app-screenage',
  templateUrl: './screenage.component.html',
  styleUrls: ['./screenage.component.scss'],
  providers: [CusViewHttpService, Commfunc]
})
export class ScreenageComponent implements OnInit {
  custAttr: any;
  custName: any;
  custterm: any;
  custNo: any;
  user: any;
  orgId: string = '';
  userId: string = '';
  targetId: string = '';
  path: any;
  screenageBean: ScreenageBean = new ScreenageBean();
  msgs: Message[] = [];

  HTTPIPDress: string;//信贷ip
  biaozhi: string;
  constructor(
    public httpService: CusViewHttpService,
    public commfunc: Commfunc
  ) {
    this.custNo = this.commfunc.getSessionData('custNo');
    this.user = JSON.parse(this.commfunc.getSessionData('commonHeader'));
    if (this.user.orgId) {
      this.orgId = this.user.orgId;
    }
    if (this.user.userId) {
      this.userId = this.user.userId;
    }
    if (this.user.targetId) {
      this.targetId = this.user.targetId;
    }

    // let biaozhi;
    // let a = this.targetId.split('');
    // let b = a[a.length - 1];
    // if (b == '0') {
    //   biaozhi = '01';
    // } else if (b == '1') {
    //   biaozhi = '02';
    // }

  }

  ngOnInit() {
    let param = {
      custNo: this.custNo
    }
    this.httpService.selectCustBriefInfo(param).subscribe(data => {
      this.custAttr = data.custAttribute;
      this.custName = data.custName;
      this.custterm = data.custType;
      if (data.dataSourceId == 'BR00A') {
        this.HTTPIPDress = TZB_HTTP_XIN;
        this.biaozhi = '01';
      } else if (data.dataSourceId == 'BR00B') {
        this.HTTPIPDress = TZB_HTTP_XIN_C;
        this.biaozhi = '02';
      } else if (!data.dataSourceId || data.dataSourceId == '') {
        this.HTTPIPDress = TZB_HTTP_XIN;
        this.biaozhi = '01';
      }
      let temp = {
        clientNo: this.custNo,
        queryManner: '1',
        emplNumber: this.userId,
        bankFlag: this.biaozhi
      }
      this.httpService.BZ_0200200003301_ERS(temp).subscribe(data => {
        this.path = data.url;
      })
    })
  }
  //电子档案卷宗
  juanzong() {
    this.screenageBean.userId = this.userId;
    this.screenageBean.sysCode = '122';
    this.screenageBean.casServerLoginUrl = 'http://10.1.39.140:8011/searchPaymentTool';
    this.screenageBean.serverName = 'ywsn';
    this.screenageBean.validateType = '0';
    this.screenageBean.extraInfo = 'ywsn';
    this.screenageBean.encryptType = 'HS256';
    let timestamp = new Date().getTime();
    let target = "custLinkForm" + timestamp;
    // this.commfunc.createBlank(target);
    this.httpService.tokenApply(this.screenageBean).subscribe(data => {
      if (data.returnCode.code == 'success') {
        let tokens = data.tokenCode;
        let url = this.path + "&token_id=" + tokens;
        let features = 'top=80,left=50,width=1180,height=768,toolbar=no,location=no,directories=0,status=1,menubar=no,scrollbars=yes,resizable=yes';
        let closeOnLoseFocus = 'closeOnLoseFocus';
        this.commfunc.openPostWindow(url, "custLinkForm" + timestamp, features, closeOnLoseFocus);
      }
    })

  }
  //查看卷宗
  LookBook() {
    this.screenageBean.userId = this.userId;
    this.screenageBean.sysCode = '122';
    this.screenageBean.casServerLoginUrl = 'http://10.1.39.140:8011/searchPaymentTool';
    this.screenageBean.serverName = 'ywsn';
    this.screenageBean.validateType = '0';
    this.screenageBean.extraInfo = 'ywsn';
    this.screenageBean.encryptType = 'HS256';
    let timestamp = new Date().getTime();
    let target = "custLinkForm" + timestamp;
    // this.commfunc.createBlank(target);
    this.httpService.tokenApply(this.screenageBean).subscribe(data => {
      if (data.returnCode.code == 'success') {
        let tokens = data.tokenCode;
        let url = this.HTTPIPDress + "plats/FrontController?token=" + tokens + "&suid=" + this.userId + "&khm=" + this.custNo + "&where=JZ&urlParam=/plats/FrontController?commandType=2018013001|moduleCode=F5B41F4330C6B747C420F2289E31BE29";
        let features = 'top=80,left=50,width=1180,height=768,toolbar=no,location=no,directories=0,status=1,menubar=no,scrollbars=yes,resizable=yes';
        let closeOnLoseFocus = 'closeOnLoseFocus';
        this.commfunc.openPostWindow(url, "custLinkForm" + timestamp, features, closeOnLoseFocus);
      }
    })
  }
  //业务维度影像
  ywVideo() {
    this.screenageBean.userId = this.userId;
    this.screenageBean.sysCode = '122';
    this.screenageBean.casServerLoginUrl = 'http://10.1.39.140:8011/searchPaymentTool';
    this.screenageBean.serverName = 'ywsn';
    this.screenageBean.validateType = '0';
    this.screenageBean.extraInfo = 'ywsn';
    this.screenageBean.encryptType = 'HS256';
    let timestamp = new Date().getTime();
    let target = "custLinkForm" + timestamp;
    // this.commfunc.createBlank(target);
    this.httpService.tokenApply(this.screenageBean).subscribe(data => {
      if (data.returnCode.code == 'success') {
        let tokens = data.tokenCode;
        let url = this.HTTPIPDress + "plats/FrontController?token=" + tokens + "&suid=" + this.userId + "&khm=" + this.custNo + "&where=YW&urlParam=/plats/FrontController?commandType=2018013001|moduleCode=F5B41F4330C6B747C420F2289E31BE29";
        let features = 'top=80,left=50,width=1180,height=768,toolbar=no,location=no,directories=0,status=1,menubar=no,scrollbars=yes,resizable=yes';
        let closeOnLoseFocus = 'closeOnLoseFocus';
        this.commfunc.openPostWindow(url, "custLinkForm" + timestamp, features, closeOnLoseFocus);
      }
    })
  }
  //客户维度影像
  khVideo() {
    this.screenageBean.userId = this.userId;
    this.screenageBean.sysCode = '122';
    this.screenageBean.casServerLoginUrl = 'http://10.1.39.140:8011/searchPaymentTool';
    this.screenageBean.serverName = 'ywsn';
    this.screenageBean.validateType = '0';
    this.screenageBean.extraInfo = 'ywsn';
    this.screenageBean.encryptType = 'HS256';
    let timestamp = new Date().getTime();
    let target = "custLinkForm" + timestamp;
    // this.commfunc.createBlank(target);
    this.httpService.tokenApply(this.screenageBean).subscribe(data => {
      if (data.returnCode.code == 'success') {
        let tokens = data.tokenCode;
        let url = this.HTTPIPDress + "plats/FrontController?token=" + tokens + "&suid=" + this.userId + "&khm=" + this.custNo + "&where=KH&urlParam=/plats/FrontController?commandType=2018013001|moduleCode=F5B41F4330C6B747C420F2289E31BE29";
        let features = 'top=80,left=50,width=1180,height=768,toolbar=no,location=no,directories=0,status=1,menubar=no,scrollbars=yes,resizable=yes';
        let closeOnLoseFocus = 'closeOnLoseFocus';
        this.commfunc.openPostWindow(url, "custLinkForm" + timestamp, features, closeOnLoseFocus);
      }
    })
  }
  //客户全影像
  khFullVideo() {
    let session = {
      custNo: JSON.parse(atob(window.sessionStorage.getItem('custNo'))),
      commonHeader: JSON.parse(atob(window.sessionStorage.getItem('commonHeader'))),
      userName: JSON.parse(window.sessionStorage.getItem('chName')).userName
    }
    this.setCookie('session', JSON.stringify(session));
    // window.open('./assets/pages/full-video.html');
    window.open('/#/pages/tzb/custom/customer-videos');
  }
  //设置cookie
  setCookie(name, value) {
    var date = new Date();//初始化时间
    date.setTime(date.getTime() + 60 * 60 * 1000);//一个小时过期
    document.cookie = name + "=" + value + ";expires=" + date.toUTCString() + ';path=/';//设置路径
  }
  //权限
  judgeField(fId) {
    let ret = this.commfunc.permissionFiledCheck(fId);
    return ret
  }
}
